import { useState } from 'react';
import { useParams } from 'react-router-dom';

import { Header } from '../../components/Header';
import { QRModal } from '../../components/Modals/QRModal';
import {
  TableContainer,
  TableHeadlineContainer,
  TableHeadlinerContentItem,
  Table,
  HeadlineHighlighedDisclaimer,
  TableHeadRow,
  TableHeadCell,
  TableCell,
  TableRow,
  TransactionValue,
  TxHash,
  TableNavigationButtons,
  TableNavigationNumbersContainer,
  TableNavigationNumberButton,
  TableNavigationTextButton,
  TxRecipient,
  BoldText,
  CoinLink,
  TableWrapper,
  TableNextNavigationTextButton,
  HeadlineContainer,
  HeadlineAddressContainer,
  HeadlineAddressHeader,
  HeadlineAddressButton,
  CopyButtonIcon,
  HeadlineAddress,
  Tooltip,
  TokenButtonSymbol,
  TokenButtonSeparator,
  QRButtonIcon,
  CoinsCounter,
  TokenButton,
  TokenButtonAmount,
  TokenButtonIconContainer,
  HeadlineCoinsContainer,
  CoinsCounterLabel,
  TokenDropdownContainer,
  TokenDropdownIcon,
} from '../AddressPage/components';
import { Transactions } from '../AddressPage/constants';

import {
  CoinDetailsContainer,
  CoinDetailsRow,
  KeyLabel,
  KeyValue,
  Container,
  Content,
  ContractLink,
  TableHeadlineDisclaimer,
  TableTabsContainer,
  TableTabButton,
} from './components';

export default function ContractPage() {
  const { contract } = useParams() as any;
  const [copyTooltip] = useState('Copy address');
  const [modal, setModal] = useState(false);

  const onClose = () => {
    setModal(false);
  };

  const showModal = () => {
    setModal(true);
  };

  const onClickCopy = () => {
    navigator.clipboard.writeText(contract);
  };

  return (
    <>
      {modal && <QRModal onClose={onClose} address={contract} />}
      <Header />
      <Container>
        <Content>
          <HeadlineContainer>
            <HeadlineAddressContainer>
              <HeadlineAddressHeader>
                {`Contract:  `}
                <HeadlineAddress>{contract}</HeadlineAddress>
              </HeadlineAddressHeader>
              <HeadlineAddressButton
                onClick={() => {
                  onClickCopy();
                }}
              >
                <CopyButtonIcon />
                <Tooltip>{copyTooltip}</Tooltip>
              </HeadlineAddressButton>
              <HeadlineAddressButton onClick={showModal}>
                <QRButtonIcon />
                <Tooltip>Click to copy QR code</Tooltip>
              </HeadlineAddressButton>
            </HeadlineAddressContainer>
            <HeadlineCoinsContainer>
              <CoinsCounterLabel>
                Coins:
                <CoinsCounter>3</CoinsCounter>
              </CoinsCounterLabel>
              <TokenDropdownContainer>
                <TokenButton>
                  <TokenButtonSymbol>ETH:</TokenButtonSymbol>
                  <TokenButtonAmount>435.3</TokenButtonAmount>
                  <TokenButtonSeparator />
                  <TokenButtonIconContainer>
                    <TokenDropdownIcon />
                  </TokenButtonIconContainer>
                </TokenButton>
              </TokenDropdownContainer>
            </HeadlineCoinsContainer>
          </HeadlineContainer>
          <CoinDetailsContainer>
            <CoinDetailsRow>
              <KeyLabel>Creation Tx hash::</KeyLabel>
              <KeyValue>
                <ContractLink to="/transaction/0xc5d2460186f7233c927e7db2dcc703c0e500}">
                  0xc5d2460186f7233c927e7db2dcc703c0e500
                </ContractLink>
              </KeyValue>
            </CoinDetailsRow>
            <CoinDetailsRow>
              <KeyLabel>Created on::</KeyLabel>
              <KeyValue>2020-06-05 13:40 (UTC) </KeyValue>
            </CoinDetailsRow>
          </CoinDetailsContainer>
          <TransactionsTable />
        </Content>
      </Container>
    </>
  );
}

export function TransactionsTable() {
  function trimAddress(address: string) {
    if (!address) {
      return '';
    }

    return `${address.slice(0, 6)}...${address.slice(-6, address.length - 1)}`;
  }

  return (
    <TableContainer>
      <TableTabsContainer>
        <TableTabButton isSelected>Transactions</TableTabButton>
        <TableTabButton>Info</TableTabButton>
      </TableTabsContainer>
      <TableHeadlineContainer>
        <TableHeadlinerContentItem>
          <TableHeadlineDisclaimer>
            {`Showing `}
            <HeadlineHighlighedDisclaimer>3</HeadlineHighlighedDisclaimer>
            {` out of `}
            <HeadlineHighlighedDisclaimer>3</HeadlineHighlighedDisclaimer>
            {` transactions`}
          </TableHeadlineDisclaimer>
          <TableNavigation />
        </TableHeadlinerContentItem>
      </TableHeadlineContainer>
      <TableWrapper>
        <Table>
          <TableHeadRow>
            <TableHeadCell>Tx Hash</TableHeadCell>
            <TableHeadCell>Type</TableHeadCell>
            <TableHeadCell>Age</TableHeadCell>
            <TableHeadCell>From</TableHeadCell>
            <TableHeadCell>To</TableHeadCell>
            <TableHeadCell>Value</TableHeadCell>
            <TableHeadCell>Coin</TableHeadCell>
            <TableHeadCell>Fee (USD)</TableHeadCell>
          </TableHeadRow>
          {Transactions.map((transaction, idx) => (
            <TableRow key={idx}>
              <TableCell>
                <TxHash to={`/transaction/${transaction.hash}`}>{transaction.hash}</TxHash>
              </TableCell>
              <TableCell>{transaction.type}</TableCell>
              <TableCell>{transaction.timestamp}</TableCell>
              <TableCell>
                {transaction.subTransactions.map((subtransaction, idx) => (
                  <TxRecipient key={idx} to={`/address/${subtransaction.from}`}>
                    {trimAddress(subtransaction.from)}
                  </TxRecipient>
                ))}
              </TableCell>
              <TableCell>
                {transaction.subTransactions.map((subtransaction, idx) =>
                  subtransaction.to ? (
                    <TxRecipient key={idx} to={`/address/${subtransaction.to}`}>
                      {trimAddress(subtransaction.from)}
                    </TxRecipient>
                  ) : (
                    <BoldText>N/A</BoldText>
                  )
                )}
              </TableCell>
              <TableCell>
                {transaction.subTransactions.map((subTransaction, idx) => (
                  <TransactionValue key={idx}>
                    {subTransaction.value ? subTransaction.value : 'N/A'}
                  </TransactionValue>
                ))}
              </TableCell>
              <TableCell>
                {transaction.subTransactions.map((subTransaction, idx) =>
                  subTransaction.coin ? (
                    <CoinLink key={idx} to={`/coin/${subTransaction.coin}`}>
                      {subTransaction.coin ? subTransaction.coin : 'N/A'}
                    </CoinLink>
                  ) : (
                    <BoldText>N/A</BoldText>
                  )
                )}
              </TableCell>
              <TableCell>
                {transaction.subTransactions.map((subTransaction, idx) => (
                  <TransactionValue key={idx}>
                    {subTransaction.fee ? `$${subTransaction.fee.toFixed(2)}` : ''}
                  </TransactionValue>
                ))}
              </TableCell>
            </TableRow>
          ))}
        </Table>
      </TableWrapper>
    </TableContainer>
  );
}

function TableNavigation() {
  const pages = [1, 2, 3, 4];
  const [selectedPage, selectPage] = useState(1);

  const onSelectPage = (idx: number) => {
    selectPage(idx);
  };

  const onClickPrevPage = () => {
    selectPage((prevPage) => prevPage - 1);
  };

  const onClickNextPage = () => {
    selectPage((prevPage) => prevPage + 1);
  };

  const onClickLastPage = () => {
    selectPage(pages[pages.length - 1]);
  };

  const onClickFirstPage = () => {
    selectPage(1);
  };

  return (
    <TableNavigationButtons>
      <TableNavigationTextButton disabled={selectedPage === 1} onClick={onClickFirstPage}>
        First
      </TableNavigationTextButton>
      <TableNextNavigationTextButton disabled={selectedPage === 1} onClick={onClickPrevPage}>
        Previous
      </TableNextNavigationTextButton>
      <TableNavigationNumbersContainer>
        {pages.map((pageIdx) => (
          <TableNavigationNumberButton
            key={pageIdx}
            isSelected={pageIdx === selectedPage}
            onClick={() => {
              onSelectPage(pageIdx);
            }}
          >
            {pageIdx}
          </TableNavigationNumberButton>
        ))}
      </TableNavigationNumbersContainer>
      <TableNextNavigationTextButton
        disabled={selectedPage === pages[pages.length - 1]}
        onClick={onClickNextPage}
      >
        Next
      </TableNextNavigationTextButton>
      <TableNavigationTextButton
        disabled={selectedPage === pages[pages.length - 1]}
        onClick={onClickLastPage}
      >
        Last
      </TableNavigationTextButton>
    </TableNavigationButtons>
  );
}
